import React, { Component } from "react";
import ".././style.css";
import "./blog-post.css";
import AuthorCard from "./authorCard";
import PageHeader from ".././header";

class blogPost26 extends Component {
  //start webpage at the top
  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div>
      {/* Cover Photo */}
      <PageHeader
        imgUrl="/blog-header.png"
        title="Blog Post #26"
      />

        {/*End of Header*/}

        {/*Start of blog post*/}
        <div class="blog-content">
          <div>
            <h2 class="blog-title">
              Personal Project Series #1 - Link Up
            </h2>
            <p class="date">27th September 2020</p>
          </div>

          <div class="post">        
            <p class="post-content">
              Apart from studying, attending lectures, completing internships, attending society events, hanging out with friends, and more, some students manage to complete incredible personal projects during their time at uni! WIT will be shining the spotlight on the amazing personal projects students at UNSW have created. 
            </p>
            <br />
         
            <p class="post-content">
              In this blog post, well be hearing from the founders of LinkUp! Perhaps you used LinkUp to discover WIT or other societies. Just like any great tool, it started with the recognition of a need, followed by a great idea. Lets dive into our conversation with the three founders!
            </p>
            <br />

            <p class="post-content">
              Hey, were Deki, Zhou, and Felicia, three UNSW 2nd year students who created LinkUp!
            </p><br/>

            <p class="question">
              Introduce yourself!
            </p>
            <p class="post-content">
              <strong>Deki(D)</strong>: Im a software engineering student that listens to one too many tech podcasts and enjoys playing Valorant.
            </p>
            <p class="post-content">
              <strong>Zhou(Z)</strong>: Just your average 2nd-year Comp Sci student. Quiet and introverted. I enjoy cooking, video games, and watching anime in my spare time.
            </p>
            <p class="post-content">
              <strong>Felicia(F)</strong>: Im a Software Eng/Commerce student who loves baking, music (I play classical guitar), and graphic design - something I get to do a lot of as WITs Marketing Exec!
            </p><br/>

            <p class="question">
              What is LinkUp?
            </p>
            <p class="post-content">
            Z: Its a uni event tracker to help find events happening at your uni.
            </p>
            <p class="post-content">F: By collating society events, we aim to help students find their people in the student community, as well as help societies reach more students.</p><br/>

            <p class="question">
              What inspired you to create LinkUp? 
            </p>
            <p class="post-content">D: I am actually from QLD, and when I first came to UNSW O-week I didnt know anyone, so I was trying my best to go to every workshop/society stalls. However, finding events and workshops around campus was difficult and scattered over multiple mediums (UNSW website, Eventbrite, Facebook), and I wondered why there wasnt an easier way to navigate events on campus.
            </p><br/>

            <p class="question">
            How did LinkUp become affiliated with Arc? How did you pitch your idea?
            </p>
            <p class="post-content">F: Arc actually reached out to us after seeing our posts on the UNSW Discussion Group! They loved how we were making it easier for students to be involved with uni life and offered their support. It was a great experience working with them.
            </p><br/>

            <p class="question">
            Who was on the founding team and what roles did you have allocated? After the project took off, were more people recruited to join the team?
            </p>
            <p class="post-content">D: LinkUp started off with me, Zhou and Felicia. I worked mainly on the frontend of the website while Zhou worked on backend and devops. Felicia was in charge of marketing. After a period of growth, yes, we did decide to recruit more members to help LinkUp become an even better product.
            </p><br/>

            <p class="question">
            Can you tell us about the process of building LinkUp? What time commitments did it 
require?
            </p>
            <p class="post-content">Z: Building LinkUp took a long time. The whole process involved splitting the project into iterations so it was easier to handle, and focused on building MVPs so we could iteratively improve them later on. We experimented with code and features and researched methods and solutions to our problem, which we defined earlier: finding events. We also talked a lot with each other before making decisions. A small part of the project was Googling and using Stack Overflow to find tutorials and code examples that we could use since we were relatively new to personal projects. In terms of time commitment, the project was very flexible and fit within our schedules. Some weeks we might have spent a few hours on it, and others felt like a full-time job (often when we had nothing else to do e.g,. summer holidays). Weve probably spent well over a few hundred hours so far on LinkUp.
            </p>
            <p class="post-content">F: In terms of building our user base and reaching students, we focused on Facebook discussion groups as the easiest way to reach a large number of students.
            </p><br/>

            <p class="question">
            What did you learn whilst creating LinkUp?
            </p>
            <p class="post-content">D: LinkUp consists of a small and dedicated team, so I learned how to work in a team and the importance of communication and time management to deploy features on time. I also learned to work with external parties to satisfy both Linkups needs and theirs. 
            </p>
            <p class="post-content">Z: I learned how to use AWS to host our website, CDN and backend, how to write scripts to scrape websites using Python and set up continuous delivery for our repo. I also learned a bit of React so I could help out with the frontend.
            </p>
            <p class="post-content">F: I have definitely developed my teamwork and communication skills along the way, but I also realised how lucky we are at UNSW to have such an active and engaged student community. Society events are a huge part of the uni experience at UNSW, in comparison to other universities, and Im really grateful for that. It would be amazing if LinkUp could help shape student communities in other unis as well.
            </p>

            <p class="question">
            What are some of the challenges you faced while creating LinkUp?
            </p>
            <p class="post-content">Z: Probably encountering a bug or error and not being able to find a fix on Google. The best thing to do here is to take a break and refresh your mind for a while, so when you come back, you can take a different approach. Another challenge was finding the motivation to do the project. This might be different for everyone, but I found setting goals and thinking about the future helped me be more motivated in creating LinkUp.
            </p><br/>

            <p class="question">
            Who did you turn to for help along the way?
            </p>
            <p class="post-content">Z: Whenever we needed help, we would either Google it, ask our teammates for help, or ask questions in relevant coding communities available to us. Some supportive communities for asking coding related questions are CSESoc workshops, Pcsoc discord, r/learn programming, and other programming subreddits, a certain UNSW society discord, and plenty of others. 
            </p>
            <p class="post-content">F: We also have talked to a wonderful mentor from UNSW Founders, who helped guide us and focus on our goals.
            </p><br/>

            <p class="question">
            What has been the most rewarding part of creating LinkUp?
            </p>
            <p class="post-content">D: Seeing students using LinkUp and receiving feedback about how much it has helped them find events around campus will continue to be the most rewarding part of Linkup. Knowing that other people are also finding value in a project that you spent so much time and effort on will serve as a motivation to improve LinkUp continually.  
            </p>
            <p class="post-content">Z: While creating LinkUp, I realised how rewarding a personal project can be and how much you can learn from the experience. Personal projects are completely different from course projects regarding how much control you have over your project. You get to choose your own teammates without any restrictions, have a flexible time commitment that fits in with your schedule, do something you are interested in, plus so many other things. Ultimately, this made it easier to learn the technologies required to build LinkUp and learn what we were interested in. I highly encourage everyone interested in software engineering to start a personal project for something that interests them. There is just so much you can learn from it.
            </p>
            <p class="post-content">F: Like Deki, for me, its knowing students are using the site and that were actually helping strengthen UNSWs student community. Every time a student or society contacts us with questions or support, its super exciting and makes me want to do more.
            </p><br/>

            <p class="question">
            What has been the initial impact of LinkUp? Have more students joined societies?
            </p>
            <p class="post-content">Z: So far we have had 7.7k users and 12k sessions. Were not too sure of how much we impacted students joining societies, but it has certainly made it easier to find events to go to.
            </p>
            <p class="post-content">F: Weve been impacted a lot by COVID, with many events being postponed or canceled. But, weve received a lot of positive feedback, so hopefully, as we move back on campus LinkUp can reach even more people.
            </p><br/>

            <p class="question">
            Whats next for LinkUp? What are your plans and goals and will you be pursuing it after 
            </p>
            <p class="post-content">Z: We hope more students will use LinkUp, so were planning to add many features to help make uni events better! We cant say much yet, but you can expect ways to give feedback for an event and a voting system. We will still maintain it after graduation, depending on how much time we have, but dont expect any major updates. This should be fine, though, as it doesnt require much time to maintain.
            </p><br/>

            <p class="question">
            What advice would you give to other students who want to pursue a personal project?
            </p>
            <p class="post-content">D: If you have a personal project you are interested in, then I would say just start it. If you need help learning the frameworks or languages, there are so many great resources with my personal favourite being Youtube and Codecademy. 
            </p>
            <p class="post-content">Z: Do something youre interested in, go to relevant workshops, use your support (founders, CSESOC, WIT, etc,) and have fun.
            </p>
            <p class="post-content">F: Think about what you want to achieve and why, write down your goals, and celebrate every victory.
            </p><br/>

            <p class="post-content">
            Deki, Zhou, and Felicia have built something that makes every students life easier and more enriching at UNSW. Not many people leave that sort of legacy! Along the way, they developed their technical skills, overcome technical challenges, and explored their interests. And of course, this project showcases their skills in a unique way that will surely be attractive to many employers. On behalf of all UNSW students, thank you Deki, Zhou, and Felicia for volunteering your time to build something we love so much! We hope you will find the knowledge and support you need to reach all your LinkUp goals.
            </p>

          </div>
        </div>

        {/*for the blog post author*/}
        <AuthorCard
        authors={{"vivian-wong":["/potraits/vivw.jpg", "profile_img vivw_img", "Vivian Wong", "Education Executive"]}}          
        />
        {/*End of blog posts*/}
      </div>
    );
  }
}
export default blogPost26;
